import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes : Routes = [
    {
        path : 'user',
        loadChildren : () => import('./user/user.module').then(m=>m.UserModule),
        data : {title : 'User'}
    },
    {
        path : 'role-permission',
        loadChildren : () => import('./role-permission/role-permission.module').then(m=>m.RolePermissionModule),
        data : {title : 'RolePermission'}
    },
    {
        path : 'end-user',
        loadChildren : () => import('./end-user/end-user.module').then(m=>m.EndUserModule),
        data : {title : 'RolePermission'}
    },
    {
        path : 'role',
        loadChildren : () => import('./role/role.module').then(m=>m.RoleModule),
        data : {title : 'Role'}
    },
    {
        path : 'user-role',
        loadChildren : () => import('./user-role/user-role.module').then(m=>m.UserRoleModule),
        data : {title : 'user-role'}
    },
    {
        path : 'user-role-activity',
        loadChildren : () => import('./user-role-activity/user-role-activity.module').then(m=>m.UserRoleActivityModule),
        data : {title : 'user-role-activity'}
    },
    {
        path : 'manage-role-permission',
        loadChildren : () => import('./manage-role-permission/manage-role-permission.module').then(m=>m.ManageRolePermissionModule),
        data : {title : 'manage-role-permission'}
    },
    {
        path : 'manageUserStation',
        loadChildren : () => import('./manage-user-station/manage-user-station.module').then(m=>m.ManageUserStationModule),
        data : {title : 'manage-user-station'}
    }
   
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class UserManagementRoutingModule{}